@import url('https://fonts.googleapis.com/css2?family=Charm:wght@400;700&display=swap');

.nav {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--back);
  color: var(--main);
  padding: 0 2em;
}

.logo {
  font-family: 'Charm', cursive;
  font-weight: 700;
  font-size: 2em;
  margin: 0;
  text-decoration: none;
  color: white;
  letter-spacing: 0.25em;
}

.navList {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
}

.navItem {
  display: flex;
  align-items: center;
}

.navLink {
  text-decoration: none;
}

.navItem > .navLink {
  color: var(--main);
  font-size: 1.5em;
  /* font-weight: 700; */
  padding: 0 0.5em;
  line-height: 1.5em;
  /* border-right: 2px solid var(--active); */
  display: flex;
  align-items: center;
}

.navItem > .navLink:hover {
  color: var(--active);
  background-color: #130c2c;
}

.active {
  color: var(--active);
}

.amount {
  color: red;
}
